.detail-view {
    width: 50%;
    height: 100%;
    padding: 10px;
    background-color: #ad0729;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}

.sprite-image {
    width: 80%;
    height: 150px;
    margin: 10px auto;
    background-color: #232323;
    border: 15px solid #353535;
    border-radius: 5px;
    color: #232323;
}

.data-wrapper {
    width: 80%;
    height: 110px;
    padding: 10px;
    margin: 0 auto;
    background-color: #232323;
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.22);
            box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.22);
    color: #fff;
}

.data-name {
    margin: 0 0 5px;
}

.data-char {
    font-size: 10px;
}


